

body {
    background: url(/dist/assets/images/login.jpg)  ;
}

.homeSec {
    img {
        width: 40px;
    }
}

.card {
    position: absolute;
    width: 20%;
    height: 50%;
    top: 19%;
    left: 41%;
    background-color: ivory;
    padding: 45px 8px;
    border-radius: 50px;
    /* box-shadow: 5px 10px 13px #28c3c0; */
    border: 3px solid #28c3c0;

    .S_header {
        color: $color3;
    }

    .container {
        .name {
            display: flex;
            margin: 10px;
        }
        input {
            padding: 8px;
            outline: none;
            border: 1px solid $color3;
            border-radius: 5px;
            margin-bottom: 12px;
            text-align: center;
        }
    }
}

