/* User Header Section */

.user_header {
    width: 100%;
    background-color: $color3;
    height: 100px;
    display: flex;
    justify-content: space-between;

    .header__right-authSection {
        display: flex;
        // justify-content: space-between;
        width: 10%;
    }

    .pro_img {
        width: 40px;
        position: absolute;
        top: 1%;
        right: 6%;
    }

    .btn_group {
        padding: 17px 8px;
        margin: 0 0 0 73px;

        a {
            button {
                cursor: pointer;
                outline: none;
                border: none;
                background: none;
                color: $color4;
                padding: 2px 2px;
            }

            .btn_border {
                border-right: 2px solid $color4;
                padding-right: 8px;

            }
        }
    }
}



.master__options {
    background-color: #fff;
    position: fixed;
    top: 62px;
    left: 14px;
    right: 9px;
    width: 98%;
    box-shadow: 5px 10px 13px #888888;
    /* margin: 12px; */
    padding: 20px 5px;
    border-radius: 15px;
    /* display: flex;
    justify-content: space-between; */

    .options {
        display: flex;
        padding: 8px 20px;
        /* justify-content: space-between; */

        select {
            margin-right: 20px;
            width: 15%;
            border-radius: 10px;
            padding: 8px;
            outline: none;
        }

        input {
            margin-right: 20px;
            width: 15%;
            border-radius: 10px;
            border: 1px solid;
            padding: 8px;
            outline: none;
        }

        .search {
            background-color: purple;
            padding: 8px 8px;
            color: var(--secondary-color);
            border-radius: 10px;
            border: none;
        }
    }
}


/* Hero Section */

.hero__container {
    width: 100%;
    height: 400px;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    /* background-color: springgreen; */

    .wrapper {
        width: 100%;
        height: 79%;
        margin: 84px 19px 0;
        display: flex;
        justify-content: space-between;

        .display-tiles {
            width: 675px;
            height: 100%;
            margin-right: 14px;
            display: flex;
            justify-content: space-between;
            z-index: 1;

            .content {
                width: 100%;
                height: 316px;
                background-color: #fff;
                position: absolute;
                z-index: -1;

                .item {
                    margin-top: 26px;
                    font-size: 20px;
                    font-family: sans-serif;

                    .alert {
                        color: tomato;
                    }
                }

                .subContent {
                    margin-top: 26px;
                    font-family: sans-serif;
                    font-weight: 200;
                }

                button {
                    font-size: 10px;
                    padding: 8px;
                    background-image: linear-gradient(90deg, #e90c43 0, #eeb02b 51%, #b98118);
                    color: var(--secondary-color);
                    border: none;
                    outline: none;
                    margin-top: 8%;
                }
            }

            img {
                position: relative;
                z-index: 99;
                clip-path: polygon(21% 0, 100% 0%, 100% 100%, 36% 100%, 0 48%);
                width: 86%;
                margin-left: 11%;
            }
        }

        .offer-tiles {
            width: 37%;
            height: 100%;
            background-color: #03506f;
            display: flex;
            z-index: 1;
            justify-content: space-between;

            h1 {
                padding: 60px 189px;
                font-size: 84px;
                color: khaki;
                display: flex;
            }
        }
    }
}



.content__holder {
    width: 100%;
    height: 100%;

    .content-heading {
        // position: absolute;
        z-index: 100;
        width: 30%;
        background-color: #fff;
        padding: 16px 89px;
        text-align: center;
        border-radius: 28px;
        margin: 18px 0 18px 551px;
        box-shadow: 5px 10px 20px #0a0a0a;
    }

    .city_tiles {
        display: flex;
        margin: 20px 241px;
        // background-color: #fffaebd7;
        

        img {
            width: 100%;
        }

        .block {
            z-index: 11;
            opacity: 1 !important;
            width: 480px;
            margin: 0 146px 0 0;

        }

        .lb {
            margin-right: 0;
        }

        .header-text {
            background-color: #fff;
            border-top-left-radius: 20px;
            border-top-right-radius: 20px;
            text-align: center;
            font-size: 24px;
            font-weight: 200;
        }
    }
}

