body {
    background: url(/dist/assets/images/login.jpg);
}



.card {
    position: absolute;
    width: 20%;
    height: 38%;
    top: 19%;
    left: 41%;
    background-color: ivory;
    padding: 45px 8px;
    border-radius: 50px;
    text-align: center;
    /* box-shadow: 5px 10px 13px #28c3c0; */
    border: 3px solid #28c3c0;

    .header {
        font-family: sans-serif;
        color: $color3;
        font-size: 24px;
        margin: 0 0 11px 140px;
    }

    form {
        .container {
            align-items: center;

            input {
                border: 2px solid $color3;
                padding: 12px;
                border-radius: 20px;
                width: 72%;
            }

            .ps {
                margin-top: 9%;
            }

            button {
                margin: 39px 0 12px 16px;
                border: none;
                padding: 12px;
                width: 30%;
                cursor: pointer;
                background-color: #28c3c0;
                color: #fbf9f9;
                border-radius: 50px;
            }
        }

        .containerFG {
            display: flex;
            justify-content: space-between;
            margin: 30px;

            a {
                button {
                    background: none;
                    outline: none;
                    border: none;
                }
            }

            
        }
    }
}

.card .card .inputs {
    margin: 61px 20px 0 20px;
    padding: 16px 22px;
}

.card .inputs input {
    width: 288px;
    padding: 12px 8px;
    border-radius: 50px;
    border: 1px solid var(--primary-color);
    text-align: center;
    outline: none;
}

.card .inputs #ip_email {
    margin-bottom: 30px;
}

.card .auth_helper {
    padding: 0px 35px 0 41px;
    display: flex;
    justify-content: space-between;
}

.card .auth_helper a {
    text-decoration: none;
    font-size: 14px;
    color: var(--primary-color);
}

.card .questionSec {
    margin-top: 20px;
}

.card .questionSec a {
    text-decoration: none;
    font-size: 14px;
    color: var(--primary-color);
    margin-left: 147px;
}