:root {
    --primary-color: #28c3c0;
    --secondary-color: #FBF9F9;
}

* {
    margin: 0;
    padding: 0;
    /* */
}

body {
    background: url(/src/assets/images/si.jpg);
}

.homeSec {
    position: absolute;
    width: 40px;
    height: 40px;
    /* background-color: #fff; */
    top: 20px;
    left: 20px;
}

.homeSec a {
    text-decoration: none;
}

.homeSec a .home {
    width: 40px;
    height: 40px;
    opacity: .7;
}

.homeSec a .home:hover {
    opacity: 1;
}

.cardH {
    position: absolute;
    width: 20%;
    height: 50%;
    top: 19%;
    left: 41%;
    background-color: ivory;
    padding: 45px 8px;
    border-radius: 50px;
    /* box-shadow: 5px 10px 13px #28c3c0; */
    border: 3px solid #28c3c0;

    .header {
        text-align: center;
        font-family: sans-serif;
        color: var(--primary-color);
        font-size: 24px;
    }
    .container {
        input {
            padding: 8px;
            outline: none;
            border: 1px solid var(--primary-color);
            border-radius: 5px;
            margin-bottom: 12px;
            text-align: center;
        }
    }
    

    #hname,#loc {
        margin-top: 12%;
    }

    #email,#c_email,#zip,#pass,#c_pass {
        width: 94%;
    }

    button {
        padding: 10px;
        border: none;
        outline: none;
        background-color: var(--primary-color);
        color: var(--secondary-color);
        border-radius: 50px;
        margin: 43px 0 0 151px;
    }

    .cancel {
        text-decoration: none;
        font-size: 10px;
        margin-left: 41%;
        letter-spacing: 5px;
        color: #28c3c0;
    }

}

