

/* -----------------------------------------------------------    HEADER ----------------------------------------------------------------- */
.header {
    padding: 14px 0 0 14px;

    img {
        width: 40px;
    }

    @include breakpoint(largescreen) {
        .header--right {
            display: none !important;
        }
    }

    @include breakpoint(desktop) {
        .header--right {
            display: none !important;
        }
    }

    @include breakpoint(laptop) {
        .header--right {
            display: none !important;
        }
    }

    @include breakpoint(mobile) {
        display: flex;
        justify-content: space-between;

        .header--right {
            display: flex;
            width: 50%;

            img {
                position: absolute;
                z-index: 1;
                width: 20px;
                height: 20px;
                top: 2.7%;
                right: 35%;
            }

            .search {
                background-color: $color1;
                opacity: .7;
                width: 220px;
                border: 2px solid $color2;
                border-top-left-radius: 10px;
                border-bottom-left-radius: 10px;
                margin: 0 0 0 10%;
            }

            .qr {
                width: 20%;
                background-color: $color1;
                opacity: .7;
                border: 2px solid $color2;
            }
        }
    }
}

/* -----------------------------------------------------------    HEADER END     ----------------------------------------------------------------- */


/* -----------------------------------------------------------    BODY START (HEAD)     ----------------------------------------------------------------- */

.header__section {
    display: flex;
    justify-content: space-between;
    height: 50px;

    .header__section--right {
        display: flex;
        width: 50%;

        img {
            position: absolute;
            z-index: 1;
            width: 20px;
            height: 20px;
            top: 7.2%;
            right: 17%;
        }

        .search {
            background-color: $color1;
            opacity: .7;
            width: 30%;
            border: 2px solid $color2;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
            margin: 0 0 0 63%;
        }

        .qr {
            width: 5%;
            background-color: $color1;
            opacity: .7;
            border: 2px solid $color2;
        }

    }

    @include breakpoint(mobile) {
        margin: 12px 0 0;

        .header__section--right {
            display: none;
        }

        .header__section--actions {
            display: flex;

            img {
                width: 20px;
                height: 20px;
                position: absolute;
                z-index: 1;
            }

            input {
                width: 70px;
                background-color: $color1;
                opacity: .7;
                border: 2px solid $color2;
                color: $color2;
                font-size: 10px;
                padding: 30px 0 0;
            }

            .msg {
                top: 9%;
                right: 51%;
            }

            .pro {
                top: 9%;
                right: 30%;
            }

            .hst {
                top: 9%;
                right: 8%;
            }
        }
    }

    @include breakpoint(laptop) {
        .header__section--right {
            display: flex;

            img {
                top: 9%;
                right: 40%;
            }

            .search {
                width: 71%;
                height: 91%;
                margin: 0 0 0 12%;
            }

            .qr {
                width: 13%;
            }
        }

        .header__section--actions {
            display: none;
        }
    }

    @include breakpoint(desktop) {
        .header__section--right {
            display: flex;

            .search {
                width: 50%;
                margin: 0 0 0 37%;
            }

            .qr {
                width: 10%;
            }

            img {
                right: 29%;
            }
        }
    }

    @include breakpoint(largescreen) {
        .header__section--right {
            display: flex;

            img {
                top: 8.2%;
                right: 18%;
            }

            .search {
                width: 33%;
                margin: 0 0 0 60%;
            }

            .qr {
                width: 5%;
            }
        }

        .header__section--actions {
            display: none;
        }
    }



}

/* -----------------------------------------------------------    BODY END (HEAD)     ----------------------------------------------------------------- */

/* -----------------------------------------------------------    BODY START (MAIN-SECTION)    ----------------------------------------------------------------- */

.main__section {
    display: flex;
    justify-content: space-between;
    height: 526px;
    margin: 12px 0 0 0;

    img {
        position: absolute;
        // display: none;
        width: 40px;
        z-index: 1;
    }

    .main__section--core {
        display: flex;
        width: 50%;
        align-items: center;

        .exp {
            width: 30%;
            margin: 0 0 0 63%;
        }

        .jmp {
            width: 7%;
        }

        input {
            opacity: .7;
            outline: none;
            text-align: center;
            border: none;
            cursor: pointer;
        }

        .pin {
            left: 47.8%;
            top: 37.3%;
            width: 25px;
        }

        .explore {
            width: 100%;
            padding: 13px 0;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
            border-right: 1px solid $color1;
            text-transform: uppercase;
            font-weight: 600;
        }

        .jump {
            padding: 27px 8px 0 8px;
            border-top-right-radius: 10px;
            border-bottom-right-radius: 10px;
            font-weight: 600;
            width: 1005;
        }
    }

    .main__section--actions {
        padding: 58px 0 0 0;

        input {
            width: 100%;
            padding: 52px 8px 0 8px;
            background-color: $color1;
            opacity: .5;
            color: white;
            font-size: 12px;
            text-align: center;
            border: 2px solid $color2;
            cursor: pointer;
        }

        input:hover {
            opacity: 1;
        }

        

        .mes {
            margin: 0 0 12px 0;
            border-top-left-radius: 20px;
        }

        .prof {
            margin: 0 0 12px 0;
        }

        .msg {
            right: 1%;
            top: 20%;
        }

        .pro {
            right: 1%;
            top: 28%;
        }

        .hst {
            right: 1%;
            top: 36%;
        }

        
    }
}

/* -----------------------------------------------------------    BODY END (MAIN-SECTION)    ----------------------------------------------------------------- */

/* -----------------------------------------------------------    BODY START (AD SECTION)    ----------------------------------------------------------------- */
.ad__section {
    height: 235px;
    margin: 12px 4px 0 4px;
    border: 2px solid $color1;
    border-radius: 20px;
    display: flex;

    img {
        height: 100%;
        border-right: 1px solid $color1;
    }
}
/* -----------------------------------------------------------    BODY END (AD SECTION)    ----------------------------------------------------------------- */

/* -----------------------------------------------------------    BODY START (FT SECTION)    ----------------------------------------------------------------- */
.ft__section {
    height: 235px;
    margin: 70px 4px 0 4px;
    border: 2px solid $color1;
    border-radius: 20px;
    display: flex;

    img {
        height: 100%;
        border-right: 1px solid $color1;
    }
}

body {
    background: url(/dist/assets/images/mainBg.jpg) fixed ;
}